import React from "react";
import ContactItem from "../Components/ContactItem";
import phone from "../img/phone.svg";
import email from "../img/emailme.svg";
import location from "../img/location.svg";

function ContactPage() {
  return (
    <div class="ContactPage">
      <h3 className="title-contact">Contact</h3>
      <div className="map-contact-sect">
        <div className="map-sect">
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d59585.603568365994!2d105.75589433495396!3d21.028675465470688!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3135ab5756f91033%3A0x576917442d674bfd!2zQ-G6p3UgR2nhuqV5LCBIw6AgTuG7mWk!5e0!3m2!1svi!2s!4v1621393793398!5m2!1svi!2s"
            allowfullscreen=""
          ></iframe>
        </div>
        <div className="contact-sect">
          <ContactItem
            title={"Phone"}
            icon={phone}
            text1={"+84 0369614068"}
            text2={"+84 0369614086"}
          />
          <ContactItem
            title={"Email"}
            icon={email}
            text1={"tranxuanthai18051995@gmail.com"}
            text2={"laptrinh1415@gmail.com"}
          />
          <ContactItem
            icon={location}
            title={"Address"}
            text1={"Ngõ 5, Phạm Văn Đồng, Mai Dịch"}
            text2={"Cầu Giấy, Hà Nội."}
          />
        </div>
      </div>
    </div>
  );
}

export default ContactPage;
