import React from "react";

function Categories({ filter, tim }) {
  return (
    <div className="buttons-seclect">
      {tim.map((cat, i) => {
        return (
          <button type="button" className=" btn-port" onClick={() => filter(cat)}>
            {cat}
          </button>
        );
      })}
    </div>
  );
}

export default Categories;
