import React from "react";
import allBlogs from "../Components/allBlogs";
// import heart from "../img/blogs/heart.svg";
// import eye from "../img/blogs/eye.svg";
import { ReactComponent as Viewicon } from "../img/blogs/eye.svg";
import { ReactComponent as Likeicon } from "../img/blogs/heart.svg";

function BlogsPage() {
  return (
    <div className="BlogsPage">
      <h3 className="title-blogs">Blogs</h3>
      <div className="gird-blogs">
        {allBlogs.map((blog) => {
          return (
            <div className="blog-product" key={blog.id}>
              <a href={blog.link} className="blog-product--link">
                <div className="product-container-content">
                  <img className="product-image" src={blog.image} alt="" />
                  <div className="product-title-icons">
                    <span className="product-title">{blog.title}</span>
                    <span className="product-icons">
                      <Viewicon className="product-icon " />
                      <span className="product-icon--view">600 </span>
                      <Likeicon className="product-icon " />
                      <span className="product-icon--like">300 </span>
                    </span>
                  </div>
                </div>
              </a>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default BlogsPage;
