import React from "react";
import { useState } from "react";
import Categories from "../Components/Categories";
import MenuItems from "../Components/MenuItems";
import portfolios from "../Components/allPortfolios";

const allCates = ["All", ...new Set(portfolios.map((item) => item.category))];

function PortfoliosPage() {
  const [menuItems, setMenuItems] = useState(portfolios);
  const [button, setButtons] = useState(allCates);
  const filter = (tim) => {
    if (tim === "All") {
      setMenuItems(portfolios);
      return;
    }
    const filteredData = portfolios.filter((item) => item.category === tim);
    setMenuItems(filteredData);
  };
  return (
    <div className="PortfolioPage">
      <h3 className="title-portfolio">Portfolio</h3>
      <div className="container-portfolios">
        <Categories filter={filter} tim={button} />
        <MenuItems menuItems={menuItems} />
      </div>
    </div>
  );
}

export default PortfoliosPage;
