import React from "react";

function SkillsSection({ skill, logo1, logo2 }) {
  return (
    <div className="skill-item-container">
      <span className="skill-name">
        <h4>{skill}</h4>
      </span>
      <span className="logo-skill">
        <img src={logo1} alt="" />
        <img src={logo2} alt="" />
      </span>
    </div>
  );
}

export default SkillsSection;
