import React from "react";
import ImageSection from "../Components/ImageSection";
import SkillsSection from "../Components/SkillsSection";
import Title from "../Components/Title";
//
import htmllogo from "../img/logoskills/html.svg";
import csslogo from "../img/logoskills/css3.svg";
import javascriptlogo from "../img/logoskills/javascript.svg";
import reactlogo from "../img/logoskills/react.svg";
import bt4logo from "../img/logoskills/bootstrap.svg";
import scsslogo from "../img/logoskills/sass.svg";
import figmalogo from "../img/logoskills/figma.svg";
import photoshop from "../img/logoskills/pts.svg";

//
function AboutPage() {
  return (
    <div className="AboutPage">
      <Title title={"Xin chào ,"} textbigger={"Mình là Thái. "} />
      <h3 className="title-aboutme">ABOUT ME</h3>
      <ImageSection
        textlower1={"Mình từng học BKHN chuyên ngành Vật liệu (2013-2019)."}
        textlower2={
          " Sau khi học xong ,mình nhận ra mình ko muốn theo ngành mà mình đã học."
        }
        textlower3={" Mình nghỉ hơn 1 năm ở nhà và bắt đầu học lập trình."}
      />
      <h3 className="title-about">SKILLS</h3>
      <div className= "skills-container">
        <SkillsSection skill={"Html ,Css"} logo1={htmllogo} logo2={csslogo} />
        <SkillsSection skill={"Javascript ,React"} logo1={javascriptlogo} logo2={reactlogo}/>
        <SkillsSection
          skill={"Boostrap ,Sass(Scss)"}
          logo1={bt4logo}
          logo2={scsslogo}
        />
        <SkillsSection
          skill={"Ui Design(Figma , Photoshop)"}
          logo1={figmalogo}
          logo2={photoshop}
        />
      </div>
    </div>
  );
}

export default AboutPage;
