import React from "react";
import { Link } from "react-router-dom";
import about from "../img/about.jpg";
function ImageSection({ textlower1, textlower2, textlower3 }) {
  return (
    <div className="ImageSection">
      <img src={about} alt="" />
      <div className="about-info">
        <div className="text-lower1 "> {textlower1}</div>
        <div className="text-lower2 "> {textlower2}</div>
        <div className="text-lower3 "> {textlower3}</div>
        <Link className="link-cv">Dowload my CV</Link>
      </div>
    </div>
  );
}

export default ImageSection;
